<template>
  <div>
    <button type="button" class="button-menu" @click="onClick">
      {{ label }}
      <svg
        width="12"
        height="8"
        viewBox="0 0 12 8"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M10.293 0.292969L5.99997 4.58597L1.70697 0.292969L0.292969 1.70697L5.99997 7.41397L11.707 1.70697L10.293 0.292969Z"
          fill="white"
        />
      </svg>
    </button>
  </div>
</template>

<script>
import { reactive } from "vue";

export default {
  name: "menu-button",

  props: {
    label: {
      type: String,
      required: true,
    },
  },

  emits: ["click"],

  setup(props, { emit }) {
    props = reactive(props);
    return {
      onClick() {
        emit("click");
      },
    };
  },
};
</script>
